<template>
  <CrudView
    entity-name="build"
    :loading="loading"
    title="Version History"
    description="You can create a new version by publishing your project from the local editor."
    empty-title="No versions here yet"
    :table="table"
    live
  >
    <template #date="{ payload }">
      <AntTooltip>
        {{ payload.date }}
        <template #title> {{ payload.distance }} </template>
      </AntTooltip>
    </template>
    <template #status="{ payload }: { payload: StatusPayload }">
      <AntPopover :open="payload.hover ? undefined : false">
        <template #content>
          <AntParagraph
            style="width: 300px; overflow: auto; font-family: monospace"
            :content="payload.hover"
            copyable
          />
        </template>
        <AntTag
          v-if="payload.text !== 'unknown'"
          :color="payload.tagColor"
          :style="{ cursor: payload.hover ? 'pointer' : 'default' }"
        >
          <CheckCircleOutlined v-if="payload.text === 'Live'" />
          <MinusCircleOutlined v-else-if="payload.text === 'Inactive'" />
          <ExclamationCircleOutlined v-else-if="payload.text === 'Failed'" />
          <CloseCircleOutlined v-else-if="payload.text === 'Aborted'" />
          <PauseCircleOutlined v-else-if="payload.text === 'Sleeping'" />
          <LoadingOutlined v-else-if="payload.text === 'Uploading'" />
          <LoadingOutlined v-else-if="payload.text === 'Deploying'" />
          <LoadingOutlined v-else-if="payload.text === 'Deactivating'" />
          {{ payload.text }}
        </AntTag>
      </AntPopover>
    </template>
  </CrudView>
  <AntModal
    v-if="inspectingBuildId"
    :footer="null"
    :open="!!inspectingBuildId"
    size="large"
    width="80%"
    :title="getModalTitle()"
    @cancel="inspectingBuildId = null"
  >
    <AntSpin v-if="!buildSpec.result.value || buildSpec.loading.value" />
    <BuildInspector v-else-if="buildSpec.result.value" :build-spec="buildSpec.result.value" />
  </AntModal>
</template>

<script lang="ts" setup>
import CrudView, { CrudTable } from '@/components/inputs/CrudView.vue';
import { dateDistance } from '@/utils';
import { asyncComputed } from '@/utils/asyncComputed';
import { getLocalizedString } from '@/utils/datetime';
import { usePolling } from '@/utils/polling';
import {
  CheckCircleOutlined,
  CloseCircleOutlined,
  ExclamationCircleOutlined,
  LoadingOutlined,
  MinusCircleOutlined,
  PauseCircleOutlined,
} from '@ant-design/icons-vue';
import { PhArrowCounterClockwise, PhCube, PhDownloadSimple } from '@phosphor-icons/vue';
import {
  Modal as AntModal,
  TypographyParagraph as AntParagraph,
  Popover as AntPopover,
  Spin as AntSpin,
  Tag as AntTag,
  Tooltip as AntTooltip,
} from 'ant-design-vue';
import { computed, onMounted, onUnmounted, ref } from 'vue';
import { useRoute } from 'vue-router';
import { Build, BuildSpec, DeployStatus, ProjectModel } from '../apis/cloud';
import BuildInspector from '../components/buildInspector/BuildInspector.vue';
import router from '../router';

const route = useRoute();
const projectId = route.params.projectId as string;
type B = { build: Build; status: DeployStatus['status'][] };

const { loading, result, refetch } = asyncComputed(async () => {
  const [builds, status] = await Promise.all([
    Build.list(projectId),
    ProjectModel.getStatus(projectId),
  ]);

  const b = builds.map((build) => ({
    build,
    status: status.filter((s) => s.buildId == build.id).map((s) => s.status),
  }));

  return { b, status };
});

const { startPolling, endPolling } = usePolling({
  task: refetch,
  interval: 10000,
});

onMounted(() => startPolling());
onUnmounted(() => endPolling());

const inspectingBuildId = ref<string | null>(null);

const buildSpec = asyncComputed(async () => {
  if (!inspectingBuildId.value) return null;
  return await BuildSpec.get(inspectingBuildId.value);
});

const getModalTitle = () => {
  const build = result.value?.b.find((b) => b.build.id === inspectingBuildId.value);

  const title = `Version ${build?.build.id.slice(
    0,
    8,
  )} - ${build?.build.createdAt.toLocaleString()}`;

  return title;
};

const buildStates: Array<{
  text: string;
  tagColor: string;
  check: (b: B) => boolean;
  loading?: boolean;
}> = [
  {
    text: 'Deploying',
    tagColor: 'blue',
    check: (b) =>
      (b.build.latest &&
        b.status.some((s) => s == 'Running') &&
        result.value?.status.some((s) => s.buildId !== b.build.id)) ??
      false,
    loading: true,
  },
  {
    text: 'Deactivating',
    tagColor: 'orange',
    check: (b) =>
      b.build.status === 'success' && !b.build.latest && b.status.some((s) => s == 'Running'),
    loading: true,
  },
  {
    text: 'Live',
    tagColor: 'green',
    check: (b) => b.build.latest && b.status.some((s) => s == 'Running'),
  },
  {
    text: 'Sleeping',
    tagColor: 'cyan',
    check: (b) => b.build.latest && b.status.length === 0,
  },
  {
    text: 'Failed',
    tagColor: 'red',
    check: (b) => b.build.latest && b.status.some((s) => s == 'Failed'),
  },
  { text: 'Inactive', tagColor: 'default', check: (b) => b.build.status === 'success' },
  {
    text: 'Aborted',
    tagColor: 'default',
    check: (b) => b.build.status === 'aborted' || b.build.status === 'aborted-by-user',
  },
  { text: 'Failed', tagColor: 'orange', check: (b) => b.build.status === 'failure' },
  {
    text: 'Deploying',
    tagColor: 'blue',
    check: (b) => b.build.status === 'in-progress',
    loading: true,
  },
  { text: 'Uploading', tagColor: 'yellow', check: (b) => b.build.status === 'pending' },
];

type StatusPayload = {
  text: string;
  tagColor: string;
  hover?: string;
};

function getStatusPayload(b: B): StatusPayload {
  return {
    text: buildStates.find((s) => s.check(b))?.text ?? 'unknown',
    tagColor: buildStates.find((s) => s.check(b))?.tagColor ?? 'default',
    hover: b.build.log ?? undefined,
  };
}

const table = computed(() => {
  const columns = [
    { title: 'Id' },
    { title: `Date` },
    { title: 'Abstra Version' },
    { title: 'Status' },
    { title: '', align: 'right' },
  ] satisfies CrudTable['columns'];
  if (!result.value) return { columns, rows: [] } as CrudTable;
  return {
    columns,
    rows: result.value.b.map((b) => ({
      key: b.build.id,
      cells: [
        {
          type: 'text',
          text: b.build.id.slice(0, 8),
        },
        {
          key: 'date',
          type: 'slot',
          payload: {
            date: getLocalizedString(b.build.createdAt, { weekday: undefined }),
            distance: dateDistance(b.build.createdAt),
          },
        },
        {
          type: 'text',
          text: b.build.abstraVersion ?? '-',
        },
        {
          key: 'status',
          type: 'slot',
          payload: getStatusPayload(b),
        },
        {
          type: 'actions',
          actions: [
            {
              icon: PhCube,
              label: 'Inspect version',
              onClick: async () => {
                inspectingBuildId.value = b.build.id;
                buildSpec.refetch();
              },
            },
            {
              icon: PhArrowCounterClockwise,
              label: 'View application logs',
              onClick: () => {
                router.push({
                  name: 'logs',
                  params: {
                    projectId,
                  },
                  query: {
                    buildId: b.build.id,
                  },
                });
              },
            },
            {
              icon: PhDownloadSimple,
              label: 'Download files',
              onClick: () => b.build.download(),
            },
          ],
        },
      ] satisfies CrudTable['rows'][number]['cells'],
    })),
  };
});
</script>

<style lang="scss" scoped>
.instructions {
  display: flex;
  flex-direction: column;

  pre {
    text-align: left;
  }
}
</style>
