<template>
  <AntList
    v-if="buildSpec.runtimes.length > 0"
    item-layout="horizontal"
    :data-source="buildSpec.runtimes"
  >
    <template #renderItem="{ item }: { item: ItemLogDTO }">
      <AntListItem>
        <template #actions>
          <AntDropdown>
            <PhDotsThreeCircle style="cursor: pointer" />
            <template #overlay>
              <AntMenu>
                <AntMenuItem @click="goToLogs(item)">
                  <div style="display: flex; align-items: center; gap: 5px">
                    <PhClockCounterClockwise />
                    <AntText> View Logs</AntText>
                  </div>
                </AntMenuItem>
              </AntMenu>
            </template>
          </AntDropdown>
        </template>
        <template #default>
          <AntSpace v-if="item.type == 'form'" size="large">
            <PhUserList />
            <AntText strong>{{ item.title }}</AntText>
            <AntText type="secondary" code>/{{ item.path }}</AntText>
          </AntSpace>
          <AntSpace v-if="item.type == 'job'" size="large">
            <PhTimer />
            <AntText strong>{{ item.title }}</AntText>
            <AntText type="secondary" code>{{ item.schedule }}</AntText>
          </AntSpace>
          <AntSpace v-if="item.type == 'hook'" size="large">
            <PhWebhooksLogo />
            <AntText strong>{{ item.title }}</AntText>
            <AntText type="secondary" code>/_hooks/{{ item.path }}</AntText>
          </AntSpace>
          <AntSpace v-if="item.type == 'script'" size="large">
            <PhScroll />
            <AntText strong>{{ item.title }}</AntText>
          </AntSpace>
        </template>
      </AntListItem>
    </template>
  </AntList>
  <div v-else>
    <AntEmpty
      description="No runtimes found. Make sure your project has forms, hooks or jobs before deploying it"
    />
  </div>
</template>

<script lang="ts" setup>
import {
  PhClockCounterClockwise,
  PhDotsThreeCircle,
  PhScroll,
  PhTimer,
  PhUserList,
  PhWebhooksLogo,
} from '@phosphor-icons/vue';
import { useRouter } from 'vue-router';
import { BuildSpec } from '../../apis/cloud';
import { ItemLogDTO } from '../../apis/cloud/build';

import {
  Dropdown as AntDropdown,
  Empty as AntEmpty,
  List as AntList,
  ListItem as AntListItem,
  Menu as AntMenu,
  MenuItem as AntMenuItem,
  Space as AntSpace,
  TypographyText as AntText,
} from 'ant-design-vue';

const router = useRouter();
const props = defineProps<{ buildSpec: BuildSpec }>();

const goToLogs = (item: ItemLogDTO) => {
  router.push({
    name: 'logs',
    params: { projectId: props.buildSpec.projectId },
    query: item.logQuery,
  });
};
</script>
